//
// Navigation
// -----------------------------------------------------------------------------

.navigation {
		position: fixed;
		width: 100%;
		z-index: 999;
    transition-property: all;
    transition-duration: $animation-duration;
    transition-timing-function: $animation-timing-function;

		&-nav {
				display: inline-block;
		}

    &-icon {
        position: absolute;
        overflow: hidden;
        right: 10px;
        visibility: hidden;
        transition-property: all;
        transition-duration: $animation-duration;
        transition-timing-function: $animation-timing-function;
        height: $navigation-icon-height;
        width: $navigation-icon-width;
        top: $navigation-icon-top;
    }

		&-wrapper {
				position: relative;
				margin: 0 auto;
				width: 1170px;
        transition-duration: $animation-duration;
        transition-timing-function: $animation-timing-function;
        border-bottom: $navigation-border-bottom;
        padding: $navigation-padding;

				a {
						text-transform: uppercase;
            color: $navigation-color;
            font-size: $navigation-font-size;
				}

				.logo {
						display: inline-block;

						img {
								max-height: 55px;
						}
				}

				.primary-nav {
				    margin: 0 0 0 75px;
				    padding: 0;

						li {
								display: inline;

								&:last-child {
										a {
												padding-right: 0;
										}
								}

								a {
										padding-right: 25px;
                    color: $navigation-color;

										&:hover {
												color: $primary-color;
										}
								}
						}
				}
		}

    &.fixed {
        position: fixed;
        z-index: 999;
        width: 100%;
        background-color: $navigation-fixed-background-color;

        .navigation-wrapper {
            padding: $navigation-fixed-padding;
            border-bottom: $navigation-fixed-border-bottom;
        }

        .navigation-icon {
            top: $navigation-fixed-icon-top;
        }
    }
}

.navigation-icon-toggle {
    position: absolute;
    top: 0;
    right: 15px;
    z-index: 999999;
    padding: 10px 35px 16px 0;
    cursor: pointer;

    &:focus {
        outline: none;
    }

    span,
    span:before,
    span:after {
        content: "";
        position: absolute;
        display: block;
        width: 35px;
        height: 3px;
        border-radius: 1px;
        background: #fff;
        cursor: pointer;
        transition: all 300ms ease-in-out;
    }

    span:before {
        top: -10px;
    }

    span:after {
        bottom: -10px;
    }

    &.active {
        span {
            background-color: transparent;
        }

        span:before,
        span:after {
            top: 0;
        }

        span:before {
            transform: rotate(45deg);
        }

        span:after {
            top: 10px;
            transform: translatey(-10px) rotate(-45deg);
        }
    }
}

@media screen and (max-width:1200px) {
		.navigation {
				&-wrapper {
						width: 90%;
						margin: 0 auto;
				}
		}
}

@media screen and (max-width:991px) {
    .navigation {
        &-wrapper {
            width: 90%;
            padding-left: 25px;
        }

        &-nav {
            position: fixed;
            background-color: #000;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 99999;
            visibility: hidden;
            opacity: 0;
            transition-property: opacity, visibility;
            transition-duration: $animation-duration;
            transition-timing-function: $animation-timing-function;

            &.open {
                visibility: visible;
                opacity: 0.9;
                transition-property: opacity;
                transition-duration: $animation-duration;
                transition-timing-function: $animation-timing-function;


                .primary-nav {
                    li {
                        opacity: 1;
                        transform: translate3d(0, 0, 0)
                    }
                }
            }
        }

        &-icon {
            visibility: visible;
        }

        nav {
            position: relative;
            top: 45%;
            transform: translatey(-45%);

            ul {
                li {
                    margin-bottom: 25px;

                    a {
                        text-transform: uppercase;
                        font-weight: 700;
                        transition-property: all;
                        transition-duration: $animation-duration;
                        transition-timing-function: $animation-timing-function;
                        font-size: $navigation-mobile-font-size;
                        color: $navigation-color;
                    }
                }

                &.primary-nav {
                    margin: 0;
                    padding: 0;
                    text-align: center;

                    li {
                        opacity: 0;
                        display: block;

                        &:first-child {
                            transition-delay: 0.05s
                        }

                        a {
                            padding-right: 0;
                        }
                    }
                }
            }
        }
    }
}
