.infobox {
    text-align: center;

    &-icon {
        font-size: 3.7rem;
        margin-bottom: 1rem;
    }

    &-number {
        font-size: 4rem;
        font-weight: 200;
    }

    &-type {
        font-size: 1rem;
        font-weight: 900;
        text-transform: uppercase;
        margin: 0 0 1rem;
        color: $primary-color;
    }
}
