.feature-icon-circle {
    font-size: $feature-icon-font-size;
    color: $feature-icon-color;
    border-radius: $feature-icon-border-radius;
    background: $feature-icon-background-color;
    line-height: $feature-icon-size;
    height: $feature-icon-size;
    width: $feature-icon-size;
}

.feature-icon-big {
    color: $feature-icon-big-color;
    font-size: $feature-icon-big-font-size;
    height: $feature-icon-big-size;
    width: $feature-icon-big-size;
    line-height: $feature-icon-big-size;
}
