.navigation-title {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    list-style: none;

    li {
        display: inline-block;

        &:after {
            padding: 0 1rem;
            content: "/";
            color: $primary-color;
        }

        &:last-child:after {
            display: none;
        }
    }
}
