//
// Block
// -----------------------------------------------------------------------------

.block {
    position: relative;
    width: 100%;
    overflow: hidden;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: $block-background-color;
    color: $block-color;
    padding: $block-padding;
    @extend .clearfix;

    // Styles
    &-color {
        color: $block-inverse-color;
        background-color: $primary-color;

        .text-muted {
            color: #444;
        }
    }

    &-picture {
        img {
            max-height: 600px;
            max-width: 100%;
        }
    }

    &-inverse {
        color: $block-inverse-color;
        background-color: $block-inverse-background-color;

        .text-muted {
            color: $block-inverse-color;
        }
    }

    &-faded {
        background-color: #e4e4e4;
    }

    &-brands {
        padding-bottom: (60px - 13px);
        text-align: center;

        img {
            margin: 0 13px 13px;
        }
    }

    &-background-icon {
        position: absolute;
        font-size: 20rem;
        opacity: 0.3;
        left: 5%;
        top: 0;
        color: inherit;
    }

    &-video,
    &-hero {
        display: table;
        width: 100%;
        height: 90vh;
    }

    &-imagehalf {
        height: 50rem;
        padding: 0;

        .sizefix {
            height: 100%;
        }
    }

    &-background-image {

      position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background: #292929;
  background-size: cover !important;
  background-position: 50% 50% !important;

    }

    &-background-cover-color {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    // Typography
    &-title {
        font-size: 3rem;
    }

    // block alignment
    &-align-top,
    &-align-bottom,
    &-align-middle {
        display: table-cell;
        vertical-align: middle;
    }

    &-align-bottom {
        vertical-align: bottom;
    }

    &-align-top {
        vertical-align: top;
    }

    &-absolute-bottom {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
    }

    &-border {
        &:before,
        &-top:before,
        &-lg:before,
        &-top-lg:before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            width: 33%;
            margin: 0 auto;
            border-top: 1px solid;
            border-top-color: #ccc;
            border-top-color: rgba(0,0,0, 0.1);
        }

        &-top,
        &-top-lg {
            &:before {
                bottom: auto;
                top: 0;
            }
        }

        &-lg,
        &-top-lg {
            &:before {
                width: 77%;
            }
        }
    }

    &-video-holder {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background: transparent;
        overflow: hidden;

        .ytplayer-container{
             position: absolute;
             top: 0;
             width: 100%;
             height: 100%;
             z-index: 0;
        }
    }
}

@include media-breakpoint-up(md) {
    .block {
        padding: $block-vertical-padding 0;
    }
}
