//
// Footers
// -----------------------------------------------------------------------------
.footer-simple {
    padding: 3rem 0;

    .icon {
        font-size: 1.5rem;
    }
}

.footer-normal {
    color: #787B7D;

    h6 {
        margin-bottom: 5px;
        color: #fff;
    }
}

.footer-large {
    h6 {
        color: $primary-color;
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            margin: 0;
            padding: 0;

            a {
                color: $gray-dark;
            }
        }
    }
}

.footer-basic {
    img {
        max-height: 60px;
    }

    p {
        font-size: 0.9rem;
        color: $gray;
    }

    ul {
        margin-top: 30px;
        list-style: none;

        li {
            float: left;
            margin-right: 15%;
            font-weight: 400;
            text-transform: uppercase;

            a {
                color: $primary-color;
            }

            &:last-child {
                margin-right: 0;
            }
        }
    }
}

@media screen and (max-width:1200px){
  	.footer-basic li{
        margin-right:10%;
    }
}

@media screen and (max-width:640px){
    .footer-basic li {
        display:block;
        text-align:left;
        padding:20px 0;
        border-bottom:dashed 1px #c7cacc;
        margin-right:0!important;
        float:none;
    }
}
